.announcement{
    position: relative;
}
.announcement__inner {
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $pink;
    font-size: 13px;
}
.announcement__close {
    width: 35px;
    height: 35px;
    position: absolute;
    right: 20px;
    text-align: center;
    line-height: 35px;
    cursor: pointer;
}